header.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
nav.navbar-middle {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 20px;
}
nav.navbar-middle > a {
    text-decoration: none;
    color: black;
}
nav.navbar-right {
    display: flex;
    gap: 20px;
}
nav.navbar-right > a {
    text-decoration: none;
    color: black;
}
a#login.hidden {
    display: none;
}
a#logout.hidden {
    display: none;
}


.profile {
    display: grid;
    grid-template-rows: 10vh auto;
}
.informations {
    display: grid;
    grid-template-rows: 10vh auto;
}
.compte {
    display: grid;
    grid-template-rows: 10vh auto;
}
.cours {
    display: grid;
    grid-template-rows: 10vh auto;
}
.content {
    display: grid;
    grid-template-columns: 14vh auto;
}